.m-navHead {

  @media screen and #{breakpoint(medium)} {
    margin-left: $from-left *-1;
  }

  @media screen and #{breakpoint(small down)} {
    line-height: 36px;
    font-size: 36px;
  }

  margin-top: $bfs * 2;
  margin-bottom: $bfs * 5;
  line-height: 64px;
  font-size: 64px;

  a {
    color: #000;
  }

  a:hover {
    color: $mi-pink;
  }

  &--logoSlash,
  &--logoSlug {
    display: inline-block;
  }

  &--logoSlash {
    color: $mi-pink;
  }

  &--logoSlug {
    font-weight: 400;
  }

  [data-toggle=offCanvas] {
    margin-bottom: 20px;

    @media screen and #{breakpoint(small down)} {
      margin-bottom: 12px;
    }
  }

  &--text {
    display: inline-block;
    font-family: $family-headlines;
    font-weight: bold;
    vertical-align: baseline;
    margin-bottom: -5px;
  }

  &--hamburger {
    font-size: 48px;
    display: inline-block;
    width: 72px;
    height: 1em;
    padding: 0;
    cursor: pointer;
    transition: transform .2s ease-in-out;
    vertical-align: middle;
    border: 0 none;
    background: transparent;
    margin-left: 8px;

    @media screen and #{breakpoint(small down)} {
      font-size: 26px;
      width: 44px;
    }
  }

  // Button height fix for Firefox
  &--hamburger::-moz-focus-inner {
    padding: 0;
    border: 0 none;
  }

  // Focus fix for Chrome
  &--hamburger:focus {
    outline: 0;
  }

  &--hamburger:before,
  &--hamburger:after {
    content: "";
  }

  &--hamburger:before,
  &--hamburger .icon,
  &--hamburger:after {
    display: block;
    width: 63px;
    height: 10px;
    margin: 0 0 .2em;
    transition: transform .2s ease-in-out;
    background: $mi-lila;

    @media screen and #{breakpoint(small down)} {
      width: 36px;
      height: 6px;
    }
  }

  // Styles for the active `.hamburger` icon
  &--hamburger[aria-expanded=true]:before,
  &--hamburger[aria-expanded=true] .icon,
  &--hamburger[aria-expanded=true]:after {
    background: $mi-pink;
  }

  // CLOSE/CANCEL/CROSS
  &--hamburger.hamburger-cancel[aria-expanded=true] .icon {
    transform: scale(0);
  }

  &--hamburger.hamburger-cancel[aria-expanded=true]:before {
    transform: translateY(.4em) rotate(135deg);
  }

  &--hamburger.hamburger-cancel[aria-expanded=true]:after {
    transform: translateY(-.4em) rotate(-135deg);
  }
}